@charset 'uft-8';
@import '_reset.scss';

/* info */
.container {
  margin-top: 60px;
  @include flex(row, center, center);
  .left {
    width: 65%;
    img {
      width: 100%;
      height: calc(100vh - 60px);
      object-fit: cover;
    }
  }
  .right {
    width: 35%;
    height: calc(100vh - 60px);
    padding: 30px 50px;

    .info {
      padding-bottom: 20px;
      border-bottom: 1px solid #ddd;

      h3 {
        margin-bottom: 20px;
        font-size: 1.3em;
        font-weight: 600;
      }
      p {
        margin-bottom: 20px;
      }
    }
  }
  .price {
    font-size: 1.2em;
  }
  .gender {
    color: rgb(110, 110, 110);
  }
  .desc {
    line-height: 1.2em;
  }
  .viewmore {
    display: block;
    margin-bottom: 20px;
    font-size: 0.9em;
    color: rgb(110, 110, 110);
    text-decoration: underline;
  }

  .rate {
    @include flex(row, center, flex-start);

    span {
      font-size: 1.2em;
    }
    span:last-child {
      margin-left: 5px;
      font-size: 0.9em;
      color: rgb(110, 110, 110);
    }
  }
  .select {
    padding: 20px 0;
    border-bottom: 1px solid #ddd;

    p {
      margin-bottom: 20px;
      font-size: 0.9em;

      span {
        font-weight: 600;
      }
    }
  }

  .thum {
    width: 85%;
    @include flex(row, center, space-between);
    a {
      display: block;
      width: 80px;
      height: 80px;
      border: 1px solid #ddd;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }
  .buy {
    padding: 20px 0;

    button {
      float: right;
      text-decoration: underline;
    }
  }
  .buyBtn {
    @include flex(row, center, space-between);
    width: 100%;
    margin-top: 50px;

    a {
      display: block;
      width: 43%;
    }
    button {
      width: 50px;
      height: 50px;
      border: 1px solid #000;
    }
  }
}
